<template>
  <div class="view aside">
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('home')">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames">
        <!-- 表单 -->
        <el-collapse-item :name="2">
          <!-- {{APP.c("dslArray:\n",APP.mock.dslArray)}} -->
          <c-table
            class="box-card"
            v-model="APP.model"
            :dsl="APP.dslArray.rows"
            :rules="APP.rules"
            :tableInfo="{cellspacing:2}"
            ref="topTabe"
          />
        </el-collapse-item>
        <!-- 影像列表 -->
        <el-collapse-item title="影像列表" :name="3">
          <div id="img-list-panel">
            <c-image-list
              v-if="modelList"
              v-model="modelList"
              @whenUploaded="APP.handleUploaded"
              @addImg="modelListAdd"
              @removeImg="modelListRemove"
              :options="{ action:'/proxy/upload'}"
              ref="imgUpload"
            ></c-image-list>
          </div>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";

export default {
  name: "HqbjHjHyjc",
  mixins: [Main],
  inject: ["APP"],
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.warn("beforeRouteEnter", to.name);
      let command = to.name;
      const initCurrentModel = {
        add() {
          /* 置空 */
          vm.model = {
            bc_fkbc: "2030047001",
            bc_jjyy: "",
            bc_fkbc_yc: "",
            bc_fkbc_jch: "",
            bc_ysqm: "",
            bc_jcrq: ""
          };
        },
        update() {
          /* 初始化新增model */
          /*           vm.$set(vm, "model", JSON.parse(JSON.stringify(vm.APP.model)));
          vm.$set( vm, "modelList", JSON.parse(JSON.stringify(vm.APP.modelTableHeaderSubList)) );
 */

          vm.model = JSON.parse(JSON.stringify(vm.APP.model));
          vm.modelList = JSON.parse(
            JSON.stringify(vm.APP.modelTableHeaderSubList)
          );
          vm.modelListAdd = [];
          vm.modelListRemove = [];
        }
      };
      initCurrentModel[command] && initCurrentModel[command]();
    });
  },
  mounted() {},
  data() {
    return {
      model: {},
      modelList: [],
      /* update分别保存新增与移除 */
      modelListAdd: [],
      modelListRemove: [],
      /* 动态数据 */
      activeNames: [2, 3]
    };
  },
  methods: {
    async goTo(command) {
      let isConfirm = await this.$confirm(`确实要放弃编辑?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      });
      if (!isConfirm) return;
      this.APP.goTo("home");
    },
    handleCheckChange() {
      let aTree = this.$refs.pickupTree.getTree();
      let isLeaf = true;
      let currentChecked = aTree.getCheckedNodes(isLeaf);
      console.log("currentChecked", currentChecked);
      let modelListStamp = [...this.modelList];

      currentChecked.map((checked, indexChecked) => {
        /* 如果已存在则跳过，不存在则增加 */
        for (let index = 0; index < modelListStamp.length; index++) {
          const element = modelListStamp[index];
          if (element.id === checked.id) {
            currentChecked[indexChecked] = element;
            return;
          }
        }
      });
      this.modelList = currentChecked;
    },
    deleteTableRow(row, index) {
      console.log("deleteTableRow", row, index);
      let stampModelTable = [...this.currentTableModelAdd];
      let target;
      const indexDelete = stampModelTable.findIndex(d => d.id === row.id);
      target = stampModelTable.splice(indexDelete, 1);
      /* 发送target到后台处理删除请求 */
      console.log("delete", target);
      /* 发送target到后台处理删除请求 */
      this.setCurrentTableModelAdd(stampModelTable);
    },
    async save() {
      /* 第一步：先上传图片，完成后=》handleUploaded */
      let vm = this;
      vm.$refs.imgUpload.abort();
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          const typeOfSave = {
            update() {
              vm.APP.modelAdd = vm.model;
              vm.APP.modelAddTableHeaderSubListAdd =
                vm.$refs.imgUpload.uploadedList;
              vm.APP.modelAddTableHeaderSubListRemove =
                vm.$refs.imgUpload.removedList;
            },
            add() {
              vm.APP.modelAdd = vm.model;
              vm.APP.modelAddTableHeaderSubListAdd =
                vm.$refs.imgUpload.uploadedList;
              vm.APP.modelAddTableHeaderSubListRemove = [];
            }
          };
          let command = vm.$route.name;
          typeOfSave[command] && typeOfSave[command]();
          vm.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
    }
  },
  computed: {}
};
</script>

