<template>
  <div id="app">
    <transition name="silde-fade">
      <keep-alive include="FnbpcFkjc">
        <router-view/>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
import { model, dslArray, rules } from "./mock.js";

let MSG_OPTIONS = {
  message: "",
  type: ""
};

export default {
  name: "AppRoot",
  mounted() {},
  provide() {
    return {
      APP: this
    };
  },
  data() {
    return {
      dslArray,
      model,
      modelTableHeaderSubList: [],
      /* 用于reset的初始化数据 add*/
      modelInit: {},
      /* update分别保存新增与移除 */
      modelAdd: model,
      modelAddTableHeaderSubListAdd: [],
      modelAddTableHeaderSubListRemove: [],
      rules /* 单独拎出来是因为有可能会动态更改校验规则 */
    };
  },
  computed: {},
  methods: {
    /* 处理图片上传 */
    async handleUploaded(imglist) {
      const loading = this.$loading();
      let MSG = {
        showClose: true,
        message: "保存失败",
        type: "warning"
      };
      try {
        let doc = { ...this.model, imglist };
        /* save */
        let response = await this.$post("/proxy/save", {
          coll: "hqbj_hj_yxjc" /* collection */,
          doc /* document */
        });

        if (response.isSuccess) {
          /* 如果是修改，需要更新currentModelAdd */
          if (this.$route.name === "update") {
            this.model(doc);
          }
          this.$router.push({ name: "home" });
          MSG = {
            showClose: true,
            message: "保存成功",
            type: "success"
          };
        } else {
          MSG = {
            showClose: true,
            message: response.data,
            type: "warn"
          };
          throw new Error(response.data);
        }
      } catch (error) {
        MSG = {
          showClose: true,
          message: error.message,
          type: "warn"
        };
        console.log(error);
      } finally {
        loading.close();
        this.$message(MSG);
      }
    },
    /* 处理图片上传 */
    goTo(command) {
      this.$router.push({
        name: command
      });
    },
    c(...args) {
      console.log.apply(console, args);
    },
    async save({ loading } = {}) {
      /* loading框 */
      loading = loading || this.$loading();
      try {
        let doc = this.modelAdd;
        doc.listAdd = this.modelAddTableHeaderSubListAdd;
        doc.listRemove = this.modelAddTableHeaderSubListRemove;
        /* save */
        let response = await this.$post("/proxy/save", {
          coll: "fnbpc_yxjc" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          /* 刷新列表 */
          MSG_OPTIONS = {
            type: "success",
            message: "保存成功"
          };
          /* 保存成功，更新当前展示的 */
          this.model = this.modelAdd;
          /* response返回数据 */
          this.modelTableHeaderSubList = this.modelAddTableHeaderSubListAdd;
          this.getAsideList();
          this.goTo("home");
        } else {
          throw new Error(response.data);
        }
      } catch (error) {
        MSG_OPTIONS = {
          type: "error",
          message: error.message
        };
      } finally {
        loading.close();
        this.$message({ showClose: true, ...MSG_OPTIONS });
      }
    },
    async remove({ loading }) {
      let isConfirm = await this.$confirm(`确实要删除当前的表单?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      });
      if (!isConfirm) return;
      loading = loading || this.$loading();
      let doc = this.model;
      try {
        let response = await this.$post("/proxy/delete", {
          coll: "fnbpc_jbzl" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          this.$message({
            showClose: true,
            message: "删除成功",
            type: "success"
          });
          this.model = {};
          this.getAsideList();
        } else {
          console.log(response.data);
          throw new Error("error");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "error"
        });
      } finally {
        loading.close();
      }
    },
    async getAsideList() {
      const loading = this.$loading();
      let condition = this.modelAsideQuery;
      try {
        let { isSuccess, data } = await this.$post("/proxy/get", {
          coll: "fnbpc_jbzl" /* collection */,
          doc: {},
          condition
        });
        if (isSuccess) {
          this.modelAsideList = data;
        }
      } catch (error) {
        console.log(error);
      }
      loading.close();
    },
    handleClickAside({ data, node, vm }) {
      console.log("handleClickAside", data);
      this.modelTableHeaderTopList = (sample =>
        [...Array(10).keys()].map(n => ({ ...sample, jcdh: n })))({
        jcxm: "检查项目",
        jcsj: Date.now(),
        jcys: "检查医生",
        jcjg: "检查结果",
        yxs: "影像数",
        wh: "操作"
      });
    },
    handleClickTableHeaderTopRow(data, node, vm) {
      console.log("handleClickTableHeaderTopRow", data);
      this.model = Object.assign(
        {
          cysj: "2018-12-26",
          sjys: Date.now(),
          hysj: "2018-12-26",
          hyys: Date.now(),
          jcdh: "11",
          hyxm: "",
          hyjg: ""
        },
        data
      );
      this.modelTableHeaderSubList = (sample =>
        [...Array(4).keys()].map(n => ({ ...sample, jcdh: n + 1 })))({
        name: "下载.png",
        url: "http://shonesinglone.leanapp.cn/imgs/cdgkpersonavatar.png"
      });
    }
  },
  watch: {
    model(newvalue) {
      console.clear();
      console.log("model", newvalue);
    }
  }
};
</script>

<style src="@c/styles/app.scss" lang="scss">
</style>
<style src="@c/styles/hqbj.scss" lang="scss">
</style>


